const month = ['', '', '', '', '']

export default {
  'url': '/columnBar',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': [
      {
        'name': '',
        'data|5': [
          {
            'name|+1': month,
            'value|10-100': 10
          }
        ]
      },
      {
        'name': '',
        'data|5': [
          {
            'name|+1': month,
            'value|10-100': 10
          }
        ]
      },
      {
        'name': '',
        'data|5': [
          {
            'name|+1': month,
            'value|10-100': 10
          }
        ]
      },
      {
        'name': '',
        'type': 'line',
        'data|5': [
          {
            'name|+1': month,
            'value|10-100': 10
          }
        ]
      }
    ]
  }
}



