const month = ['', '', '', '', '']

export default {
  'url': '/barWithLine',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': [
      {
        'name': '',
        'data|5': [
          {
            'name|+1': month,
            'value|-50-100': -50
          }
        ]
      },
      {
        'name': '',
        'data|5': [
          {
            'name|+1': month,
            'value|-50-100': -50
          }
        ]
      },
      {
        'name': '',
        'data|5': [
          {
            'name|+1': month,
            'value|-50-100': -50
          }
        ]
      },
      {
        'name': '',
        'type': 'line',
        'data|5': [
          {
            'name|+1': month,
            'value|-50-100': -50
          }
        ]
      }
    ]
  }
}



